import React from 'react';
import { Collapse } from 'antd';
import "./style.scss";
const Sec3Collapse = (props) => {
    const { Panel } = Collapse;
    function callback(key) {
        console.log(key);
    }
    return (
            <Collapse
                defaultActiveKey={['1']}
                onChange={callback}
                expandIconPosition='right'
            >
                <Panel header={props?.header} key="1" >
                    <div>{props?.text}</div>
                </Panel>
            </Collapse>
    )
}
export default Sec3Collapse;