import React from 'react';
import { Row, Col } from 'antd';
import "./style.scss"
const PageHeader = (props) => {
    return (
        <div className="common-page-header">
            <Row className={props?.img ? "container pt-20 justify-between" : "container py-20" }>
                <Col lg={16}  sm={24}>
                    <h1 className="font-bold m-0"> {props?.heading}</h1>
                    <p className="mt-4"> {props?.p}</p>
                </Col>
                <Col lg={8}  sm={24}>
                    {props?.img &&
                        <img className="w-full border-color" src={props?.img} alt="img" />}
                </Col>
            </Row>
        </div>
    )
}

export default PageHeader;