import React, { useState } from 'react';
import { Col, Divider } from "antd";
import "./style.scss"
const Sec4Box = (props) => {
  const [hover, setHover] = useState(false);
  return (
    <Col lg={4} className="section4-img-text p-5 text-center">
      <div className="flex justify-center">
        <img src={props?.img} alt="img" />
      </div>
      <p onMouseOver={() => setHover(true)} onMouseOut={() => setHover(false)} className={!hover ? "pt-8 m-0 text-lg font-bold cursor-pointer" : " pt-8 m-0 text-lg font-bold cursor-pointer hover"}  >{props?.li}</p>
      <Divider className={hover ? "ant-divider-hover" : ""} />
    </Col>
  )
}
export default Sec4Box;