import React from 'react'
import { Col } from 'antd';
import { withRouter } from 'react-router-dom';
import Logo from '../../assets/images/logo.png'
import Button from '../button';
import './style.scss';
const Head = (props) => {
    let {
        history, location, isMobile
    } = props;
    return (
        <>
            <header>
                <div className={`flex justify-between items-center ${isMobile ? "" : " container"} `}>
                    <Col lg={8} >
                        <img src={Logo} alt="logo" className="h-40" />
                    </Col>
                    <Col lg={16}>
                        <div className="flex  justify-end items-center">
                            <li
                                className={location?.pathname === "/" ? "hover" : ""}
                                onClick={() => {
                                    history.push("/")
                                }}>
                                Home
                    </li>
                            <li className={location?.pathname === "/study-resources" ? "hover" : ""}
                                onClick={() => {
                                    history.push("study-resources")
                                }}>
                                Study Resources
                    </li>
                            <li className={location?.pathname === "/about-us" ? "hover" : ""}
                                onClick={() => {
                                    history.push("about-us")
                                }}>
                                About
                    </li>
                            <li className={location?.pathname === "/blogs" ? "hover" : ""}
                                onClick={() => {
                                    history.push("blogs")
                                }}>
                                Blog
                    </li>
                            <li className={location?.pathname === "/content" ? "hover" : ""}
                                onClick={() => {
                                    history.push("content")
                                }}>
                                Content
                    </li>
                            <Button type="transparent" text="Login" size="small" />
                            <Button type="colored" text="Signup" size="small" />
                        </div>
                    </Col>

                </div>
            </header>
        </>
    )
}

export default withRouter(Head);