import React, { useState } from 'react';
import { Row, Col, Card, Divider, Checkbox } from 'antd';
import Button from "../../button";
import FeatureCard from './card';
import "./style.scss";
const Section2 = () => {
    const [checked, setChecked] = useState(false);
    const onChange = e => {
        setChecked(e.target.checked)
    };
    return (
        <div className="container">
            <Row className="justify-between">
                <Col lg={15} sm={24} className="my-20">
                    <FeatureCard title="Features" />
                </Col>
                <Col className="plan-card" lg={8} sm={24}>
                    <Card>
                        <h1 className="">$ 10 <sub>/month</sub></h1>
                        <Divider />
                        <p>One Time Payment</p>
                        <Checkbox
                            checked={checked}
                            onChange={onChange}
                        >
                           USD25 4-Months Plan
                    </Checkbox>
                        <Divider />
                        <div className="mb-5">
                            <Button type="transparent" text="Add to Cart" size="large" />
                        </div>
                        <Button type="colored" text="Buy Now" size="large" />
                    </Card>
                </Col>
            </Row>
        </div>
    )
}
export default Section2;